export const pageQuery = graphql`
  query getListPostByTag {
    listPostByTag: allMarkdownRemark(
      filter: { frontmatter: { tag: { in: ["basic"] } } }
    ) {
      edges {
        node {
          id
          excerpt(pruneLength: 115)
          content: excerpt
          frontmatter {
            date(formatString: "MMMM DD, YYYY")
            slug
            tag
            title
            thumbnail
          }
        }
      }
    }
  }
`
