import * as React from "react"
import Post from "./post"

const ListPost = ({ data }) => {

    return <div className="blog-box wow fadeInUp">
        <div className="row">
            {
                data && data.map(post => {
                    return <div key={post.node.id} className="col-lg-4">
                        <Post data={post.node} />
                    </div>
                })
            }
        </div>
    </div>
}

export default ListPost